export default {
	props: {
		info: {
			type: Object,
			default: {}
		},
		showDetail: {
			type: Boolean,
			default: false
		},
		showButton: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {};
	},
	methods: {
		show() {
			this.$emit('showInfo');
		},
		get() {
			//领取优惠券
			this.$emit('get');
		},
		money(m) {
			// 去小数
			let end = m.indexOf('.');
			return m.substr(0, end);
		},
		decimals(n) {
			let end = n.indexOf('.');
			return n.substr(end, 3);
		}
	}
};
